import React, { useEffect, useState } from "react";
import Header from "../commonComponent/header";
import SideBar from "../commonComponent/sideBar";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useAlert } from "react-alert";
import { useQuery } from "@tanstack/react-query";
import {
  deleteContent,
  deleteTrendingContent,
  getContent,
  trendingList,
} from "../../apiServices/home/homeHttpService";
import { showAlert } from "../commonComponent/alertManager";
import { RotatingLines } from "react-loader-spinner";

function Content() {
  const [loader, setLoader] = useState(false);
  const [delId, setDelId] = useState("");
  const alert = useAlert();

  const { data: results, refetch } = useQuery({
    queryKey: ["ContentList"],
    queryFn: getContent,
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results,
  });

  const content = results?.contents || [];

  const delContent = async (id) => {
    setLoader(true);
    const response = await deleteContent(id);
    if (!response.error) {
      setLoader(false);
      document.getElementById("popUpClose2").click();
      showAlert(alert, response.message, { timeout: 3000 });
      refetch();
    } else {
      setLoader(false);
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };

  const { data: results2, refetch: refetch2 } = useQuery({
    queryKey: ["ContentList2"],
    queryFn: trendingList,
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results,
  });

  const content2 = results2?.data || [];

  const delContent2 = async (id) => {
    setLoader(true);
    const response = await deleteTrendingContent(id);
    if (!response.error) {
      setLoader(false);
      document.getElementById("popUpClose3").click();
      showAlert(alert, response.message, { timeout: 3000 });
      refetch2();
    } else {
      setLoader(false);
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };

  return (
    <>
      <Header />

      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <div className="d-flex justify-content-end">
            <Link to="/admin/add-trending-content" className="comman_btn w-fit">
              Add Trending Content
            </Link>
            <Link to="/admin/add-content" className="comman_btn w-fit ms-2">
              Add Content
            </Link>
          </div>
          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row align-items-center justify-content-between py-md-4 py-3 px-md-3 px-2">
                <div className="col-md-auto mb-md-0 mb-3">
                  <div className="headleft">
                    <h2> Trending Content Management</h2>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="row comman_table pt-md-3 pb-md-4 pb-3 px-md-3 px-2">
                <div className="col-12">
                  <div className="table-responsive">
                    <table className="table table-hover">
                      <thead>
                        <tr>
                          <th>S.No.</th>
                          <th>Image</th>
                          <th>CONTENT SECTION</th>
                          <th className="ps-4">Action</th>
                        </tr>
                      </thead>
                      <tbody>
                        {content2?.map((user, index) => (
                          <tr key={user._id}>
                            <td>{index + 1}</td>
                            <td>
                              <div className="user-img">
                                <img
                                  src={user.image || ""}
                                  alt=""
                                  className="w-100 h-100"
                                />
                              </div>
                            </td>
                            <td>{user.titleEn}</td>
                            <td>
                              <div className="d-flex">
                                <Link
                                  to={`/admin/view-trending-content/${user._id}`}
                                  className="Table_btn me-2"
                                >
                                  <i className="fa fa-eye" />
                                </Link>

                                <Link
                                  to=""
                                  className="Table_btn"
                                  onClick={() => setDelId(user._id)}
                                  data-bs-toggle="modal"
                                  data-bs-target="#popUp3"
                                >
                                  <i className="fa fa-trash" />
                                </Link>
                              </div>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row align-items-center justify-content-between py-md-4 py-3 px-md-3 px-2">
                <div className="col-md-auto mb-md-0 mb-3">
                  <div className="headleft">
                    <h2>Content Management</h2>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="row comman_table pt-md-3 pb-md-4 pb-3 px-md-3 px-2">
                <div className="col-12">
                  <div className="table-responsive">
                    <table className="table table-hover">
                      <thead>
                        <tr>
                          <th>S.No.</th>
                          <th>CONTENT SECTION</th>
                          <th className="ps-4">Action</th>
                        </tr>
                      </thead>
                      <tbody>
                        {content?.map((user, index) => (
                          <tr key={user._id}>
                            <td>{index + 1}</td>
                            <td>{user.titleEn}</td>
                            <td>
                              <div className="d-flex">
                                <Link
                                  to={`/admin/view-content/${user._id}`}
                                  className="Table_btn me-2"
                                >
                                  <i className="fa fa-eye" />
                                </Link>
                                <Link
                                  to={`/admin/edit-content/${user._id}`}
                                  className="Table_btn me-2"
                                >
                                  <i className="fa fa-edit" />
                                </Link>
                                <Link
                                  to=""
                                  className="Table_btn"
                                  onClick={() => setDelId(user._id)}
                                  data-bs-toggle="modal"
                                  data-bs-target="#popUp2"
                                >
                                  <i className="fa fa-trash" />
                                </Link>
                              </div>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        className="modal fade popUp"
        id="popUp2"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabIndex={-1}
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-body border-0 py-4 px-3">
              <div className="popUp_main text-center">
                <img
                  className="main_icon"
                  src="../assets/img/reject_imgg.webp"
                  alt=""
                />
                <h3>Are you sure you want to delete?</h3>
                <div className="row mx-0 mt-4 mb-2">
                  <div className="col-6">
                    <Link
                      className="bg-main status_accepted_bg w-100 d-block"
                      data-bs-dismiss="modal"
                      to=""
                      onClick={() => setDelId("")}
                      id="popUpClose2"
                    >
                      No
                    </Link>
                  </div>
                  <div className="col-6">
                    <button
                      className="bg-custom-secondary status_accepted_bg w-100"
                      onClick={() => delContent(delId)}
                      disabled={loader}
                    >
                      {loader ? (
                        <>
                          <span className="me-2">Wait</span>
                          <RotatingLines
                            strokeColor="white"
                            strokeWidth="5"
                            animationDuration="0.75"
                            width="18"
                            visible={true}
                          />
                        </>
                      ) : (
                        "Yes"
                      )}
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div
        className="modal fade popUp"
        id="popUp3"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabIndex={-1}
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-body border-0 py-4 px-3">
              <div className="popUp_main text-center">
                <img
                  className="main_icon"
                  src="../assets/img/reject_imgg.webp"
                  alt=""
                />
                <h3>Are you sure you want to delete?</h3>
                <div className="row mx-0 mt-4 mb-2">
                  <div className="col-6">
                    <Link
                      className="bg-main status_accepted_bg w-100 d-block"
                      data-bs-dismiss="modal"
                      to=""
                      onClick={() => setDelId("")}
                      id="popUpClose3"
                    >
                      No
                    </Link>
                  </div>
                  <div className="col-6">
                    <button
                      className="bg-custom-secondary status_accepted_bg w-100"
                      onClick={() => delContent2(delId)}
                      disabled={loader}
                    >
                      {loader ? (
                        <>
                          <span className="me-2">Wait</span>
                          <RotatingLines
                            strokeColor="white"
                            strokeWidth="5"
                            animationDuration="0.75"
                            width="18"
                            visible={true}
                          />
                        </>
                      ) : (
                        "Yes"
                      )}
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Content;
